    width 100%
    height 100%
    display flex
    align-items center
    justify-content center
    .inner
      position relative
      width 200px
      height 200px
      text-align center
      color #ffe0a1
      background url("./img/circle-bg.png") no-repeat center center
      background-size 100% 100%
      &:before
        content ''
        position absolute
        top 0
        left 50%
        margin-left -100px
        z-index -1
        width 200px
        height 200px
        background url("./img/bg-light.png") no-repeat center center
        background-size 100% 100%
        animation rotate 4s linear infinite
      .value
        font-size .3rem
        font-family 'Benmo'
        line-height .6rem
        margin-top .5rem
